<template>

  <div class="surface-card p-4 shadow-2 border-round w-full lg:w-6">
    <div class="text-center mb-5">
      <img src="/custom_logo.PNG" alt="Image" height="50" class="mb-3">
      <div class="text-900 text-3xl font-medium mb-3">{{$t("message.welcome")}}</div>
      <span class="text-600 font-medium line-height-3">Don't have an account?</span>
      <a class="font-medium no-underline ml-2 text-blue-500 cursor-pointer">Create today!</a>
    </div>
    <div class="card card-container">
      <Form @submit="handleLogin" :validation-schema="schema">
        <div class="form-group">
          <label for="username" class="block text-900 font-medium mb-2">Username</label>
          <Field name="username" type="text" class="form-control w-full mb-3" style="padding:10px;"/>
          <ErrorMessage name="username" class="error-feedback" />
        </div>
        <div class="form-group">
          <label for="password" class="block text-900 font-medium mb-2">Password</label>
          <Field name="password" type="password" class="form-control w-full mb-3" style="padding:10px;"/>
          <ErrorMessage name="password" class="error-feedback" />
        </div>


        <div class="form-group">

          <button style="background:transparent; border:none;" :disabled="loading">
            <span
              v-show="loading"
              class="spinner-border spinner-border-sm"
            ></span>
            <Button label="Sign In" icon="pi pi-user" class="w-full"></Button>
          </button>
        </div>

        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">
            {{ message }}
          </div>
        </div>
      </Form>
    </div>
  </div>
</template>

<script>
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";

export default {
  name: "Login",
  components: {
    Form,
    Field,
    ErrorMessage,
  },
  data() {
    const schema = yup.object().shape({
      username: yup.string().required("Username is required!"),
      password: yup.string().required("Password is required!"),
    });

    return {
      loading: false,
      message: "",
      schema,
      checked1: false,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },
  methods: {
    handleLogin(user) {
      this.loading = true;

      this.$store.dispatch("auth/login", user).then(
        () => {
          this.$router.push("/profile");
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },
};
</script>

<style scoped>
body {
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.error-feedback {
  color: red;
}
input[type="button"] {
  background-color: #a61e4d;
  color: #eee;
  padding: 15px 25px;
  border: none;
}

input[type="button"]:hover {
  background-color: #d6336c;
}
</style>
